import React, { useEffect, useState } from "react";
import socketIOClient from "socket.io-client";
import ChatBoxReciever, { ChatBoxSender } from "./ChatBox";
import InputText from "./InputText";
import UserLogin from "./ChatUserLogin";
import "../styles/chat.css";

export default function ChatContainer() {
  let socketio = socketIOClient("http://localhost:80");
  const [chats, setChats] = useState([]);
  const [user, setUser] = useState(sessionStorage.getItem("Nickname"));

  const [chatOn, setChatOn] = useState(false);

  //  chat     chats 
  useEffect(() => {
    socketio.on("chat", (senderCharts) => {
      setChats(senderCharts);
    });
    //   
    if (document.getElementById("box")) {
      const chatDiv = document.getElementById("box");
      chatDiv.scrollTop = chatDiv.scrollHeight;
    }
  });

  // chat    
  function sendChatToSocket(chat) {
    socketio.emit("chat", chat);
  }

  function addMessage(chat) {
    const newChat = { ...chat, user };
    //        
    setChats([...chats, newChat]);
    //  
    sendChatToSocket([...chats, newChat]);
  }

  //     storage
  function logout() {
    // localStorage.clear();
    setUser("");
    setChatOn(false);
  }

  function ChatsList() {
    return chats.map((chat, i) => {
      if (chat.user === user)
        return (
          <ChatBoxSender key={i} message={chat.message} user={chat.user} />
        );
      return (
        <ChatBoxReciever key={i} message={chat.message} user={chat.user} /> 
      );
    });
  }

  return (
    <div>
      {chatOn ? ( //       
        <div class="container col-9 m-1 p-3 bg-light rounded shadow-lg mx-auto ">
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            {/*  */}
            <div class="container">
              <div class="row">
                <div class="col-1">
                  <h3 onClick={() => logout()}>
                    <i class="bi bi-arrow-left arrow "></i>
                  </h3>
                </div>

                <div class="col-6  mx-auto ">
                  <div class="container pt-1 bg-dark rounded-pill">
                    <h4 class="text-center text-light">:{user}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/*  */}
          <div id="box" class="container  rounded chat_container">
            <ChatsList />
          </div>

          {/*  */}
          <div class="container pt-3">
            <InputText addMessage={addMessage} />
          </div>
        </div>
      ) : (
        //    Login
        <UserLogin setUser={setUser} chatOn={chatOn} setChatOn={setChatOn} />
      )}
    </div>
  );
}
