/* eslint-disable*/
import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
// import { faker } from "@faker-js/faker";
import logo from "../logo.png";
import "../styles/Login_Header.css";

function Header() {
  // faker.locale = "ko";

  const ID = sessionStorage.getItem("ID");
  const NickName = sessionStorage.getItem("Nickname");
  const myJWT = sessionStorage.getItem("JWT");

  let navigate = useNavigate();

  const [address, setAddress] = useState("");

  //   
  //    
  //  

  useEffect(() => {
    myJWT == null ? navigate("/") : navigate("/Main");
    const URL =
      "https://geolocation-db.com/json/2725d960-5eef-11ed-9b62-857a2b26943e";
    fetch(URL)
      .then((res) => res.json())
      .then((data) => setAddress(data));
  }, []);

  // console.log(faker.address.cityName());
  // faker.internet.userName()  
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-light navbar-light ">
        <div className="container-fluid">
          <Link to="/main" class="navbar-brand">
            <img
              src={logo}
              class="d-inline-block align-middle rounded p-1"
              alt="        "
              width="150"
            />
          </Link>

          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>

          {/* nav-item */}
          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            {/*     */}
            {myJWT != null && (
              <>
                <ul class="navbar-nav ms-auto mb-2 mb-lg-0">
                  <li className="nav-item me-4 ms-auto mt-1">
                    <span className="align-middle">
                       :{" "}
                      <b>
                        {address?.city}({address?.country_code})
                      </b>
                    </span>
                  </li>

                  <li className="nav-item me-4 ms-auto mt-1">
                    <Link
                      to="/Mypage"
                      style={{ textDecoration: "none", color: "Black " }}
                    >
                      {/* {NickName ? (
                    <p className="nav-link disabled  col-12 ">
                      ! <b>{NickName}</b> !
                    </p>
                  ) : (
                    <p className="nav-link disabled  col-12 ">
                      ! <b>{ID}</b> !
                    </p>
                  )} */}
                      {/* NickName && */}
                      {
                        <span className="align-middle ">
                          ! <b>{NickName}</b> !
                        </span>
                      }
                      {/* <p className="nav-link disabled  col-12 ">
                    ! <b>{NickName}</b> 
                  </p> */}
                    </Link>
                  </li>

                  <li className="nav-item ms-auto">
                    <button className="btn btn-secondary press_btn me-2 mt-1">
                      <Link
                        to="/Mypage"
                        style={{ textDecoration: "none", color: "white " }}
                      >
                        
                      </Link>
                    </button>
                  </li>

                  <li className="nav-item ms-auto">
                    <button
                      className="btn btn-secondary press_btn me-2 mt-1"
                      onClick={(e) => {
                        e.preventDefault();
                        sessionStorage.clear();
                        navigate("/");
                      }}
                    >
                      
                    </button>
                  </li>
                </ul>
              </>
            )}
          </div>
        </div>
      </nav>
    </>
  );
}

export default Header;
